        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
        :title="form._id.length > 0 ? '' : ''"
      >
        <v-card color="var(--color-deepblue)" class="d-flex justify-center align-center">
          <div class="dialogbody">
            <v-form>
              <v-card-title>
                <span class="text-h5 mx-auto mb-6" style="font-weight: 700; color: var(--color-bule)" v-if="form._id"></span>
                <span class="text-h5 mx-auto mb-6" style="font-weight: 700; color: var(--color-bule)" v-else></span>
              </v-card-title>
              <v-btn icon style="position: absolute; top: 0px; right: 0" @click="dialog = false"><v-icon>mdi-close</v-icon></v-btn>
              <v-row>
                <v-col cols="12">
                  <v-text-field label="" hint=" 1  10 " :rules="inputRules1" v-model="form.pname"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-autocomplete v-model="form.category.big" :items="Object.keys(categories)" label=""></v-autocomplete>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-autocomplete v-model="form.category.small" :items="categories[form.category.big]" label=""></v-autocomplete>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field label="" v-model="form.size"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-autocomplete v-model="form.sunit" :items="['mm', 'cm', 'm', '', '', '']" label="" multiple></v-autocomplete>
                </v-col>
                <v-col cols="12">
                  <v-autocomplete
                    v-model="form.technology"
                    :items="[
                      'Illustrator',
                      'Photoshop',
                      'Indesign',
                      'PowerPoint',
                      'Word',
                      'Figma',
                      'JS',
                      'CSS',
                      'JQ',
                      'HTML',
                      'SCSS',
                      'Vue',
                      'Node',
                      'Premiere',
                      'After Effects',
                      'Lightroom',
                      'Final Cut ProX',
                      'Sketch Up',
                      'AutoCAD',
                      'Rhino',
                      'V-ray',
                      '3D MAX',
                      'Revit',
                      'Lumion',
                      ''
                    ]"
                    label=""
                    multiple
                  ></v-autocomplete>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field label="" v-model="form.workingday"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field label="" prefix="$" v-model="form.price"></v-text-field>
                </v-col>
                <v-col cols="12 d-flex justify-center">
                  <img-inputer
                    accept="image/*"
                    v-model="form.image"
                    size="large"
                    bottomText=""
                    hoverText=""
                    placeholder=""
                  ></img-inputer>
                </v-col>
              </v-row>
              <v-col cols="12">
                <div>
                  <v-textarea
                    fluid
                    solo
                    counter
                    name="input-7-4"
                    :rules="rules"
                    filled
                    v-model="form.description"
                    label=" 100 "
                    auto-grow
                    class="mt-2"
                  ></v-textarea>
                </div>
              </v-col>
              <v-radio-group row>
                <v-switch v-model="form.sell" :label="``"></v-switch>
                <v-spacer></v-spacer>
                <v-card-actions>
                  <v-btn color="blue darken-1" plain @click="resetForm" class="rsBtn textRed"></v-btn>
                  <v-btn color="blue darken-1" plain @click="submitModal" v-if="form._id" class="rsBtn2 textYel" :disabled="dialogSubmitting"></v-btn>
                  <v-btn color="blue darken-1" plain type="submit" @click="submitModal" :disabled="dialogSubmitting" v-else class="rsBtn2 textYel"></v-btn>
                </v-card-actions>
              </v-radio-group>
            </v-form>
